<template>
  <nav>
    <div class="nav-wrapper green">
      <div class="container">
        <router-link to="/" class="brand-logo">Employee Manager</router-link>
        <ul class="right">
          <li v-if="isLoggedIn" class="email black-text">{{currentUser}}</li>
          <li v-if="isLoggedIn">
            <router-link to="/" class="brand">Dashboard</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/login" class="brand">Login</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/register" class="brand">Register</router-link>
          </li>
          <li v-if="isLoggedIn">
            <button v-on:click="logout" class="btn black">Logout</button>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import firebase from "firebase";
export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true
      this.currentUser = firebase.auth().currentUser.email
    }
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          // this.$router.push('/login')
          this.$router.go({path: this.$router.path})
        })
    }
  }
};
</script>

<style scoped>
  .email{
    padding-right: 10px;
  }
</style>


