<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Employee ID# : {{employe_id}}</li>
      <li class="collection-item">Departement : {{dept}}</li>
      <li class="collection-item">Position : {{position}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteEmployee" class="btn red">Trash</button>
    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{name: 'edit-employee', params: {employe_id: employe_id}}"
        class="btn-floating btn-large red"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseinit";
export default {
  name: "view-employee",
  data() {
    return {
      employe_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employe_id", "==", to.params.employe_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.employe_id = doc.data().employe_id,
            vm.name = doc.data().name,
            vm.dept = doc.data().dept,
            vm.position = doc.data().position;
          })
        })
      })
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employe_id", "==", this.$route.params.employe_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employe_id = doc.data().employe_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          });
        });
    },
    deleteEmployee() {
      if (confirm("Are You Sure?")) {
        db.collection("employees")
          .where("employe_id", "==", this.$route.params.employe_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>


