<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <form @submit.prevent="updateEmployee" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" readonly v-model="employe_id" required />
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="name" required />
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="dept" required />
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="position" required />
        </div>
      </div>
      <button type="submit" class="btn">Save</button>
      <router-link v-bind:to="{name: 'view-employee', params : {employe_id: this.employe_id}}" class="btn grey">Cancel</router-link>
    </form>
  </div>
</template>

<script>
import db from "./firebaseinit";
export default {
  name: "edit-employee",
  data() {
    return {
      employe_id: null,
      name: null,
      dept: null,
      position: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("employe_id", "==", to.params.employe_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            (vm.employe_id = doc.data().employe_id),
              (vm.name = doc.data().name),
              (vm.dept = doc.data().dept),
              (vm.position = doc.data().position);
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("employees")
        .where("employe_id", "==", this.$route.params.employe_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.employe_id = doc.data().employe_id;
            this.name = doc.data().name;
            this.dept = doc.data().dept;
            this.position = doc.data().position;
          });
        });
    },
    updateEmployee() {
      db.collection("employees")
        .where("employe_id", "==", this.$route.params.employe_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                employe_id: this.employe_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              })
              .then(() => {
                this.$router.push({
                  name: "view-employee",
                  params: { employe_id: this.employe_id }
                });
              });
          });
        });
    }
  }
};
</script>


